<template>
  <div class="container">
    <h1>开发者工具箱</h1>

    <div class="tabs">
      <button
          v-for="(name, key) in panels"
          :key="key"
          :class="['tab-btn', { active: currentPanel === key }]"
          @click="currentPanel = key"
      >
        {{ name }}
      </button>
    </div>

    <!-- JSON 格式化 -->
    <div v-show="currentPanel === 'json'" class="panel active">
      <h2>JSON 格式化与校验</h2>
      <textarea v-model="jsonInput" placeholder="粘贴你的 JSON..."></textarea>
      <div>
        <button @click="formatJson">格式化</button>
        <button @click="copyResult">复制结果</button>
        <button @click="clearAll">清空</button>
      </div>
      <div v-if="jsonError" class="error">{{ jsonError }}</div>
      <pre v-if="jsonOutput" class="result">{{ jsonOutput }}</pre>
    </div>

    <!-- JSON 转 Java Bean -->
    <div v-show="currentPanel === 'java'" class="panel">
      <h2>JSON 转 Java Bean</h2>
      <div class="class-name">类名: <input v-model="className" placeholder="默认 Model"></div>
      <textarea v-model="jsonForJava" placeholder="粘贴 JSON..."></textarea>
      <div>
        <button @click="convertToJava">生成 Java Bean</button>
        <button @click="copyJavaCode">复制代码</button>
        <button @click="clearJava">清空</button>
      </div>
      <div v-if="javaError" class="error">{{ javaError }}</div>
      <pre v-if="javaCode" class="result">{{ javaCode }}</pre>
    </div>

    <!-- 图片转 Base64 -->
    <div v-show="currentPanel === 'base64'" class="panel"
         @dragover.prevent
         @drop.prevent="handleDrop">
      <h2>图片转 Base64</h2>
      <div class="upload-area" @click="$refs.fileInput.click()"
           :class="{ 'drag-over': isDragging }"
           @dragenter.prevent="isDragging = true"
           @dragleave.prevent="isDragging = false"
           @drop="onDrop">
        {{ isDragging ? '松开鼠标上传图片' : '点击上传图片 或 拖拽至此' }}
        <input type="file" ref="fileInput" @change="handleImageUpload" accept="image/*" style="display:none"/>
      </div>
      <img v-if="imagePreview" :src="imagePreview" class="preview"/>
      <div v-if="base64Result">
        <button @click="copyBase64">复制 Base64</button>
        <button @click="clearImage">清空</button>
        <div class="result" style="max-height: 200px; overflow: auto;">{{ base64Result }}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data() {
    return {
      currentPanel: 'json',
      panels: {
        json: 'JSON 格式化',
        java: 'JSON → Java Bean',
        base64: '图片 → Base64',
        svg: 'SVG → PNG (建设中...)'
      },
      // JSON 格式化
      jsonInput: '',
      jsonOutput: '',
      jsonError: '',
      // JSON 转 Java
      jsonForJava: '',
      className: 'Model',
      javaCode: '',
      javaError: '',
      // 图片转 Base64
      imagePreview: '',
      base64Result: '',
      isDragging: false,
    }
  },
  methods: {
    // --- JSON 格式化 ---
    formatJson() {
      this.jsonError = '';
      try {
        const obj = JSON.parse(this.jsonInput);
        this.jsonOutput = JSON.stringify(obj, null, 2);
      } catch (e) {
        this.jsonError = 'JSON 格式错误: ' + e.message;
        this.jsonOutput = '';
      }
    },
    copyResult() {
      if (this.jsonOutput) {
        this.copyToClipboard(this.jsonOutput);
      }
    },
    clearAll() {
      this.jsonInput = '';
      this.jsonOutput = '';
      this.jsonError = '';
    },

    // --- JSON 转 Java Bean ---
    convertToJava() {
      this.javaError = '';
      try {
        const obj = JSON.parse(this.jsonForJava);
        this.javaCode = this.generateJavaBean(obj, this.className || 'Model');
      } catch (e) {
        this.javaError = 'JSON 格式错误: ' + e.message;
        this.javaCode = '';
      }
    },
    generateJavaBean(obj, className) {
      let code = `import lombok.Data;\n\n@Data\npublic class ${className} {\n`;
      for (const [key, value] of Object.entries(obj)) {
        // 驼峰转换: user_name → userName
        const camelKey = key.replace(/_([a-z])/g, (_, c) => c ? c.toUpperCase() : '');
        // 类型推断
        let type = 'String';
        if (typeof value === 'number') {
          type = Number.isInteger(value) ? 'Integer' : 'Double';
        } else if (typeof value === 'boolean') {
          type = 'Boolean';
        } else if (value === null) {
          type = 'Object';
        }
        code += `    private ${type} ${camelKey};\n`;
      }
      code += '}';
      return code;
    },
    copyJavaCode() {
      if (this.javaCode) {
        this.copyToClipboard(this.javaCode);
      }
    },
    clearJava() {
      this.jsonForJava = '';
      this.javaCode = '';
      this.javaError = '';
    },

    copyBase64() {
      if (this.base64Result) {
        this.copyToClipboard(this.base64Result);
      }
    },
    clearImage() {
      this.imagePreview = '';
      this.base64Result = '';
      this.$refs.fileInput.value = '';
    },

    // 通用复制
    copyToClipboard(text) {
      navigator.clipboard.writeText(text).then(() => {
        alert('已复制到剪贴板！');
      }).catch(() => {
        // 兼容旧浏览器
        const textarea = document.createElement('textarea');
        textarea.value = text;
        document.body.appendChild(textarea);
        textarea.select();
        document.execCommand('copy');
        document.body.removeChild(textarea);
        alert('已复制到剪贴板！');
      });
    },
    onDrop(e) {
      this.isDragging = false;
      const files = e.dataTransfer.files;
      if (files && files[0]) {
        this.processFile(files[0]);
      }
    },
    handleImageUpload(e) {
      const file = e.target.files[0];
      if (file) {
        this.processFile(file);
      }
    },
    processFile(file) {
      if (!file.type.startsWith('image/')) {
        alert('请选择图片文件');
        return;
      }

      const reader = new FileReader();
      reader.onload = (event) => {
        this.imagePreview = event.target.result;
        this.base64Result = event.target.result;
      };
      reader.readAsDataURL(file);
    }
  }
}
</script>

